* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

.list-side-bar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 1em !important;
  width: 100%;
  padding-top: 12px;
  padding-bottom: 12px;
}

.active-nav {
  background-color: #dfeefc !important;
  border-left: 3px solid rgb(26, 26, 219);
  transition: all 0.2s linear;
}

.segment-list-left,
.segment-list-right {
  overflow-y: scroll;
  height: 800px;
  padding: 2em;
  width: 100%;
  background-color: #f3f6f8 !important;
}

.segment-list-left h2 {
  color: #7b8fa5;
}

.segment-list-left h1 {
  font-size: 1.4em;
  margin-top: 1em;
}

.segment-list-left span {
  margin-right: 10px;
}

.card {
  margin-top: 1em;
  padding: 5px;
}

.card h3 {
  font-size: 1.5em;
  margin-top: 15px;
}

.card-left {
  text-align: center;
  padding: 0;
}

.right-answer-card {
  border-right: 3px solid #18a0fb;
}

.MuiCardActions-root {
  justify-content: center;
}

.makeStyles-drawerOpen-7 {
  width: 170px !important;
}

.custom-btn {
  margin: 0.6em 0 !important;
  padding: 12px 5px;
  text-transform: lowercase !important;
}

.bg-btn {
  background: #ffffff !important;
}

.makeStyles-drawer-6 {
  white-space: normal !important;
}

.right-answer {
  border: 3px solid #18a0fb !important;
}
